import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { ThemeContext } from '../ThemeContext';

const Navbar = () =>
{
    const theme = useContext(ThemeContext)
    return (
        <section className="bdsm_navsect">
            <nav className="bdsm_navcomp">
                <a href="/" className="bdsm_header">Bristol Road</a>
                <div id="bdsm_navbtns">
                    <Link to="events">
                        Events
                    </Link>
                    <Link to="ib">
                        IB Program
                    </Link>
                </div>
                <button className={`bdsm_themeSwitcher ${Boolean(theme.theme) ? 'light' : 'dark'}`} onClick={() => theme.setTheme(Number(!Boolean(theme.theme)))}></button>
            </nav>
        </section>
    );
};

export default Navbar;